@import url(https://fonts.googleapis.com/css?family=Raleway:400,500);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
display: flex;
height: 100vh;
justify-content: center;
align-items: center;
  text-align: center;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}


#quote-box{
  display: table;
  Padding: 10px;
  margin: center;
  width: 450px;
  background: white;
  border-radius: 5px;
  -webkit-transition: 1s;
  transition: 1s;
}

#text{
  font: 400 3em Raleway
   
}
#author{
  font: italic 1.5em Raleway;
  margin: 0px 20px 10px 0;
  text-align: right;
}
#buttons{
  display: flex;
  justify-content: space-around;
}
#new-quote{
  display: block;
  margin: 0px 0.2rem 10px 280px;
  height: 25px;
  width: 150px;
  border: none;
  border-radius: 3px;
  color: white;
  font: 500 1em Raleway;
}
#tweet{
  border-radius: 3px;
  border: none;
  box-shadow: none;
  width: 40px;
  height: 40px;
}
